.scheduledPostsAreaWrapper {
  display: grid;
  grid-template-columns: 1fr;
  .top {
    background: #fff;

    .header {
      display: flex;
      padding: 15px;
      border-bottom: 1px solid #e9ecfa;
      span {
        font-family: ProximaNova-Regular;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        color: #010947;
      }
    }
    .content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 40px 0;
      .caption {
        font-family: ProximaNova-Regular;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        text-align: center;
        color: #42434b;
        margin-top: 20px;
      }
    }
  }
  .bottom {
    .containerHeader {
      font-family: ProximaNova-Regular;
      font-style: normal;
      font-weight: 600;
      font-size: 16px;
      line-height: 16px;
      color: #010947;
      margin-bottom: 20px;
    }
    .post {
      background: #ffffff;
      border: 1px solid #eaedf3;
      box-sizing: border-box;
      box-shadow: 0px 4px 4px 5px rgba(234, 234, 234, 0.25);
      border-radius: 6px;
      padding: 20px 10px;
      display: grid;
      grid-template-columns: 1fr;
      gap: 10px;
      margin-top: 24px;
      .postContent.gallery4 {
        display: grid;
        gap: 5px;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr;
        figure {
          margin-bottom: 0;
        }
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          display: block;
        }
        .galleryItem1 {
          grid-area: 1 / 1 / span 1 / span 2;
        }
        .galleryItem2 {
          grid-area: 1 / 3 / span 2 / span 2;
        }
        .galleryItem3 {
          grid-area: 2 / 1 / span 3 / span 2;
        }
        .galleryItem4 {
          grid-area: 3 / 3 / span 2 / span 2;
          position: relative;
          cursor: pointer;
          p {
            position: absolute;
            color: #fff;
            top: 45%;
            left: 45%;
            font-size: 50px;
          }
        }
      }

      .header {
        display: flex;
        .leftArea {
          display: flex;
          gap: 10px;
          .profile-icon {
            width: 54.22px;
            height: 54.69px;
            border-radius: 50%;
          }
          .profileDetails {
            display: flex;
            gap: 8px;
            flex-direction: column;
            .name {
              font-family: ProximaNova-Regular;
              font-style: normal;
              font-weight: 600;
              font-size: 14px;
              line-height: 14px;
              color: #42434b;
            }
            .position {
              font-family: ProximaNova-Regular;
              font-style: normal;
              font-weight: normal;
              font-size: 12px;
              line-height: 99.7%;
              color: #656565;
            }
            .time {
              font-family: ProximaNova-Regular;
              font-style: normal;
              font-weight: 600;
              font-size: 12px;
              line-height: 12px;
              color: #42434b;
            }
          }
        }
        .rightArea {
          margin-left: auto;
          cursor: pointer;
        }
      }
      .post-description {
        margin-top: 10px;
        p {
          font-family: ProximaNova-Regular;
          font-style: normal;
          font-weight: 600;
          font-size: 14px;
          line-height: 20px;
          color: #42434b;
          width: 60%;
        }
      }
      .shareDetails {
        display: flex;
        justify-content: space-between;
        span {
          font-family: ProximaNova-Medium;
          font-size: 12px;
          line-height: 25px;
          color: #42434b;
        }
      }
      .line {
        border-bottom: 1px solid #e5e5e5;
      }
      .actionButtons {
        display: flex;
        justify-content: space-between;
        cursor: pointer;
        .action:nth-child(2) {
          margin-left: 27px;
        }
        .action {
          display: flex;
          gap: 5px;
          align-items: center;
        }
      }

      .replySection {
        .comment {
          grid-template-columns: 0.05fr 1fr;
        }
        .reply {
          grid-template-columns: 0.13fr 1fr;
          margin-top: 5px;
          .profile-img {
            justify-self: flex-end;
          }
          .reply-img {
            display: flex;
            justify-content: flex-end;
          }
        }
        .comment,
        .reply {
          display: grid;
          gap: 10px;
          .profile-img {
            width: 36.78px;
            height: 37.31px;
            border-radius: 50%;
          }
          .comment-Text {
            display: flex;
            background: #f4f5fc;
            padding: 5px;
            .leftArea {
              display: flex;
              flex-direction: column;
              gap: 5px;
              .name {
                font-family: ProximaNova-Regular;
                font-style: normal;
                font-weight: 600;
                font-size: 12px;
                line-height: 12px;
                color: #42434b;
              }
              .position {
                font-family: ProximaNova-Regular;
                font-style: normal;
                font-weight: normal;
                font-size: 10px;
                line-height: 10px;
                color: #656565;
              }
              .content {
                font-family: ProximaNova-Regular;
                font-style: normal;
                font-weight: normal;
                font-size: 12px;
                line-height: 15px;
                color: #42434b;
              }
            }
            .rightArea {
              margin-left: auto;
            }
          }
        }
        .writeAComment {
          display: grid;
          grid-template-columns: 0.05fr 1fr;
          img {
            width: 36.78px;
            height: 37.31px;
            border-radius: 50%;
          }
          input {
            border: none;
            :focus {
              outline: none;
            }
          }
        }
        .moreComments {
          margin-top: 10px;
          font-family: ProximaNova-Regular;
          font-style: normal;
          font-weight: 600;
          font-size: 12px;
          line-height: 12px;
          color: #42434b;
        }
        .actionIcons {
          display: flex;
          align-items: center;
          gap: 30px;
          img {
            margin-right: 10px;
          }
          img,
          span {
            cursor: pointer;
          }
        }
      }
    }
  }
}
